.question {
    font-size: 17px;
}

.category {
    font-size: 12px;
}

.loading {
    height: 500px;
    position: relative;
}

.loading h4 {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.question_card {
    margin-top: 5%;
    background-color: #f5f5f5;
}


